import React, { useState } from 'react';
import { Redirect, Route, Switch} from 'react-router-dom';
import {
  IonApp,
  IonIcon,
  IonLabel,
  IonRouterOutlet,
  IonTabBar,
  IonTabButton,
  IonTabs,
  isPlatform,
  IonGrid,
  IonRow,
  IonHeader,
  IonToolbar,
  IonButtons,
  IonButton,
  IonTitle
} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { chatbubbles, person, home, addCircle, chatbubblesOutline} from 'ionicons/icons';
import Home from './pages/Home';
import Account from './pages/Account';
import Messages from './pages/Messages';
import './App.css'
/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';
import { stringify } from 'querystring';

const App = () => {


  const [authState, setAuthState] = 
  useState<{
    token: null | string, 
    userId: null | string
  }>({
      token: null, 
      userId: null
    });
  
  const [tabsState, setTabsState] = 
  useState<{
    tabsPlacement: "top" | "bottom" | undefined
    tabsLayout: "icon-top" | "icon-start" | "icon-end" | "icon-bottom" | "icon-hide" | "label-hide" | undefined
  }>(isPlatform("desktop") ? {
      tabsPlacement: "top", tabsLayout: "icon-start"
    } : {
      tabsPlacement: "bottom", tabsLayout: undefined    }
    );

  const login = (token: string, userId: string) => {
    setAuthState({token: token, userId: userId});
  };

  const logout = () => {
    setAuthState({token: null, userId: null});
  }

  return (
  <IonApp>
    <IonReactRouter>
    <IonHeader id="desktop-ion-header">
      </IonHeader>
      <IonTabs>
        <IonRouterOutlet>
          <Route path="/home" component={Home} exact={true} />
          <Route path="/account" component={Account} exact={true} />
          <Route path="/messages" component={Messages} />
          <Route path="/" render={() => <Redirect to="/home" />} exact={true} />
        </IonRouterOutlet>
        <IonTabBar slot={tabsState.tabsPlacement}>
          <IonTabButton tab="home" href="/home" layout={tabsState.tabsLayout}>
            <IonIcon icon={home} />
            <IonLabel>Home</IonLabel>
          </IonTabButton>
          <IonTabButton tab="messages" href="/messages" layout={tabsState.tabsLayout}>
            <IonIcon icon={addCircle} />
            <IonLabel>Create Post</IonLabel>
          </IonTabButton>
          <IonTabButton tab="account" href="/account" layout={tabsState.tabsLayout}>
            <IonIcon icon={person} />
            <IonLabel>Account</IonLabel>
          </IonTabButton>
        </IonTabBar>
      </IonTabs>
    </IonReactRouter>
  </IonApp>
  )
};

export default App;
